
'use client';
import { useState } from 'react';
import type { Vehicle } from '@/lib/types';
import { Button } from '../ui/button';
import { PlusCircle, Edit, Trash2 } from 'lucide-react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { formatPrice } from '@/lib/utils';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

import { Input } from '../ui/input';
import { Label } from '../ui/label';
import { Textarea } from '../ui/textarea';
import { SubmitButton } from '../submit-button';
import { useToast } from '@/hooks/use-toast';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../ui/select';
import { useRouter } from 'next/navigation';

async function handleVehicleAction(command: 'add' | 'edit' | 'delete', payload: any) {
    const res = await fetch('/api/vehicle-manager', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ command, ...payload }),
    });
    return await res.json();
}

function VehicleForm({ vehicle, onFormSubmit }: { vehicle?: Vehicle | null, onFormSubmit: () => void }) {
    const { toast } = useToast();
    const [isOpen, setIsOpen] = useState(false);

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const formData = new FormData(event.currentTarget);
        const data: any = {
            make: formData.get('make'),
            model: formData.get('model'),
            year: Number(formData.get('year')),
            price: Number(formData.get('price')),
            mileage: Number(formData.get('mileage')),
            type: formData.get('type'),
            description: formData.get('description'),
            images: (formData.get('images') as string).split(',').map(s => s.trim()),
            specifications: [
                 { name: "Engine", value: formData.get('spec-engine') || ""},
                 { name: "Transmission", value: formData.get('spec-transmission') || ""},
                 { name: "Drivetrain", value: formData.get('spec-drivetrain') || ""}
            ]
        };

        const command = vehicle ? 'edit' : 'add';
        const payload = vehicle ? { data, vehicleId: vehicle.id } : { data };
        const result = await handleVehicleAction(command, payload);

        if (result.message.includes('success')) {
            toast({ title: 'Success!', description: result.message });
            setIsOpen(false);
            onFormSubmit();
        } else {
            toast({ variant: 'destructive', title: 'Error', description: result.message });
        }
    };
    
    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild>
                {vehicle ? (
                     <Button variant="ghost" size="icon"><Edit className="h-4 w-4" /></Button>
                ) : (
                    <Button><PlusCircle className="mr-2 h-4 w-4" /> Add New Vehicle</Button>
                )}
            </DialogTrigger>
            <DialogContent className="sm:max-w-xl">
                 <DialogHeader>
                    <DialogTitle>{vehicle ? 'Edit Vehicle' : 'Add New Vehicle'}</DialogTitle>
                    <DialogDescription>
                        {vehicle ? 'Update the details for this vehicle.' : 'Fill out the form to add a new vehicle to the inventory.'}
                    </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSubmit} className="grid max-h-[70vh] grid-cols-1 gap-4 overflow-y-auto p-1 md:grid-cols-2">
                    {/* Form fields */}
                    <div className="space-y-2"><Label>Make</Label><Input name="make" defaultValue={vehicle?.make} required /></div>
                    <div className="space-y-2"><Label>Model</Label><Input name="model" defaultValue={vehicle?.model} required /></div>
                    <div className="space-y-2"><Label>Year</Label><Input name="year" type="number" defaultValue={vehicle?.year} required /></div>
                    <div className="space-y-2"><Label>Price</Label><Input name="price" type="number" defaultValue={vehicle?.price} required /></div>
                    <div className="space-y-2"><Label>Mileage</Label><Input name="mileage" type="number" defaultValue={vehicle?.mileage} required /></div>
                    <div className="space-y-2">
                        <Label>Type</Label>
                        <Select name="type" defaultValue={vehicle?.type} required>
                            <SelectTrigger><SelectValue placeholder="Select a type" /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="SUV">SUV</SelectItem>
                                <SelectItem value="Sedan">Sedan</SelectItem>
                                <SelectItem value="Truck">Truck</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    <div className="space-y-2 md:col-span-2"><Label>Description</Label><Textarea name="description" defaultValue={vehicle?.description} required /></div>
                    <div className="space-y-2 md:col-span-2"><Label>Image IDs (comma-separated)</Label><Input name="images" defaultValue={vehicle?.images.join(', ')} required /></div>
                    <div className="space-y-2 md:col-span-2">
                        <h4 className="font-medium">Specifications</h4>
                        <div className="grid grid-cols-3 gap-2">
                           <div className="space-y-2"><Label>Engine</Label><Input name="spec-engine" defaultValue={vehicle?.specifications.find(s=>s.name === 'Engine')?.value} /></div>
                           <div className="space-y-2"><Label>Transmission</Label><Input name="spec-transmission" defaultValue={vehicle?.specifications.find(s=>s.name === 'Transmission')?.value} /></div>
                           <div className="space-y-2"><Label>Drivetrain</Label><Input name="spec-drivetrain" defaultValue={vehicle?.specifications.find(s=>s.name === 'Drivetrain')?.value} /></div>
                        </div>
                    </div>
                    <DialogFooter className="md:col-span-2">
                        <DialogClose asChild><Button type="button" variant="secondary">Cancel</Button></DialogClose>
                        <SubmitButton label={vehicle ? 'Save Changes' : 'Add Vehicle'} icon={vehicle ? <Edit /> : <PlusCircle />} />
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}


function DeleteVehicleDialog({ vehicleId, onDeleted }: { vehicleId: number, onDeleted: () => void }) {
    const { toast } = useToast();

    const handleDelete = async () => {
        const result = await handleVehicleAction('delete', { vehicleId });
        if (result.message.includes('success')) {
            toast({ title: 'Success!', description: 'Vehicle deleted successfully.' });
            onDeleted();
        } else {
            toast({ variant: 'destructive', title: 'Error', description: result.message });
        }
    };
    
    return (
        <AlertDialog>
            <AlertDialogTrigger asChild>
                <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive"><Trash2 className="h-4 w-4" /></Button>
            </AlertDialogTrigger>
            <AlertDialogContent>
                <AlertDialogHeader>
                    <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                    <AlertDialogDescription>
                        This action cannot be undone. This will permanently delete the vehicle from your inventory.
                    </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                    <AlertDialogAction onClick={handleDelete} className="bg-destructive hover:bg-destructive/90">Delete</AlertDialogAction>
                </AlertDialogFooter>
            </AlertDialogContent>
        </AlertDialog>
    );
}

export default function VehicleManager({ initialVehicles }: { initialVehicles: Vehicle[] }) {
    const router = useRouter();

    const refreshData = () => {
      router.refresh();
    };

    return (
        <div className="mt-6">
            <div className="flex justify-end mb-4">
                <VehicleForm onFormSubmit={refreshData} />
            </div>
            <div className="rounded-lg border">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>ID</TableHead>
                            <TableHead>Vehicle</TableHead>
                            <TableHead>Type</TableHead>
                            <TableHead>Price</TableHead>
                            <TableHead className="text-right">Actions</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {initialVehicles.map(vehicle => (
                            <TableRow key={vehicle.id}>
                                <TableCell>{vehicle.id}</TableCell>
                                <TableCell className="font-medium">{vehicle.year} {vehicle.make} {vehicle.model}</TableCell>
                                <TableCell>{vehicle.type}</TableCell>
                                <TableCell>{formatPrice(vehicle.price)}</TableCell>
                                <TableCell className="text-right">
                                    <VehicleForm vehicle={vehicle} onFormSubmit={refreshData} />
                                    <DeleteVehicleDialog vehicleId={vehicle.id} onDeleted={refreshData} />
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </div>
        </div>
    );
}
